import React from 'react';
import { connect } from "react-redux";
import Todo from './Todo';
import { addTodo, removeTodo, calcSum } from "../../../redux/marketsReducer";
import { onChangeAC, onSubmitTodoAC } from "../../../redux/inputReducer";
import Preloader from './../../common/Preloader';

class TodoContainer extends React.Component {
    render() {
        return (this.props.markets.fetching) ? <Preloader /> : <Todo props={this.props} />
    }
}

let mapStateToProps = (state, ownProps) => ({
    marketName: ownProps.market,
    market: state.markets[ownProps.market],
    markets: state.markets,
    input: state.input
});

export default connect(mapStateToProps, 
    {  onChangeAC, removeTodo, onSubmitTodoAC, calcSum, addTodo })(TodoContainer);